.app {
    width: 100vw;
    height: 100vh;
    background-image: url('./background-min.jpg');
    background-size: cover;
    position: relative;
}

.title {
    height: 100%;
    width: 60%;
    position: absolute;
    left: 50%;
    -webkit-transform: translate(-50%, 0%);
        -ms-transform: translate(-50%, 0%);
            transform: translate(-50%, 0%);
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}

#find-a-docter {
    color: white;
    font-weight: 1000;
    font-size: 3em;
    text-align: center;
    -webkit-animation: animation forwards 1.5s;
            animation: animation forwards 1.5s;
    font-family: gugi;
    -ms-flex: 1 1;
        flex: 1 1;
}

#supply-medicine {
    color: white;
    font-weight: 500;
    font-size: 1.5em;
    font-family: gugi;
    text-align: center;
    -webkit-animation: animation forwards 2s;
            animation: animation forwards 2s;
    -ms-flex: 1 1;
        flex: 1 1;
}

.frame-wrap {
    -ms-flex: 8 1;
        flex: 8 1;
    position: relative;
    overflow: hidden;
}


